@import "colors";
@import "mixins";

.rent {
  position: relative;
  &__desc {
    margin: 30px 0;
    &:after {
      content: '';
      clear: both;
      display: table; }
    img {
      float: left;
      width: 250px;
      margin-right: 25px; } }
  &__items {
    padding: 10px 0 30px;
    display: flex;
    flex-wrap: wrap;
    &::after {
      content: '';
      clear: both;
      display: table; } }
  &__item {
    width: 47%;
    background: #fff;
    padding: 15px;
    margin-bottom: 18px;
    box-sizing: border-box;
    transition: all 0.3s ease;
    box-shadow: 0px 0px 5px 1px rgba(0,0,0,0.2);
    &:hover {
      background: linear-gradient(to bottom, rgba(184,184,184,0) 0%, rgba(184,184,184,0.2) 100%); }
    &:nth-child(odd) {
      margin-right: 30px; }
    &:nth-child(even) {
      float: right; }
    &-content {
      display: flex; }
    .title {
      font-weight: bold;
      margin: 10px 0 20px;
      cursor: pointer;
      &:hover {
        color: $blue; } }
    .document {
      margin-right: 15px;
      cursor: pointer;
      &:hover {
        img {
          border: 3px solid $sky; } }
      img {
        width: 100px;
        border: 3px solid transparent;
        transition: all 0.3s ease; }
      p {
        text-align: center;
        color: $blue; } }
    .desc {
      font-size: 0.9em; }
    ol {
      margin-top: 15px; }
    li {
      white-space: nowrap; }
    .btns {
      display: flex;
      margin-top: 15px;
      a:first-child {
        margin-right: 30px; } } }
  .modal {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 60px 70px 60px 60px;
    background: #fff;
    box-shadow: 0px 0px 11px 2px rgba(0,0,0,0.4);
    display: none;
    opacity: 0;
    z-index: 99999;
    &__content {
      width: 40vw;
      img {
        max-width: 100%; } }
    &__close {
      position: absolute;
      top: -55px;
      right: -50px;
      width: 35px;
      height: 40px;
      cursor: pointer; } }
  .overlay {
    z-index: 9999;
    position: fixed;
    background-color: rgba(#000, 0.5);
    opacity: 1;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    cursor: pointer;
    display: none; } }
